.getStarted {
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 0.2em;
  margin-left: 0.7em;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.05), -3px 0px 5px rgba(0, 0, 0, 0.05);
  transition: all 0.2s;
  cursor: pointer;
}

.getStarted:active {
  box-shadow: none;
}

